<template>
    <view class="xinyi-content">
        <xinyi-nav-bar title="兵棋模式" leftIcon="undo-filled" right-text="退出登录"></xinyi-nav-bar>

        <view class="product-list__view">
            <view class="product-item" v-for="(item, index) in productList" :key="index" @click="toDetail(item.id)">
                <uni-card spacing="0" padding="6rpx" margin="6rpx">
                    <image :src="item.coverUrl" class="product-cover__image" mode="aspectFit"></image>
                    <view class="product-name__view">{{ item.productName }}</view>
                </uni-card>
            </view>
        </view>
        <uni-pagination
            class="page"
            :total="total"
            :current="queryParams.pageNum"
            @change="pageChange"
            :pageSize="queryParams.pageSize"
        ></uni-pagination>
    </view>
</template>

<script>
import { getOssById } from '@/api/system.js';
import { queryArmoryList } from '@/api/product.js';
export default {
    data() {
        return {
            queryParams: {
                pageSize: 10,
                pageNum: 1
            },
            total: 0,
            productList: []
        };
    },
    onLoad() {
        this.queryArmoryList();
    },
    methods: {
        pageChange(e) {
            console.log('e: ', e);
            this.queryParams.pageNum = e.current;
            this.queryArmoryList();
        },
        async queryArmoryList() {
            this.productList = [];
            let res = await queryArmoryList(this.queryParams);
            this.productList = res.data.rows;
            this.productList.forEach(async (item) => {
                if (item.cover) {
                    let oss = await getOssById(item.cover);
                    let coverUrl = this.baseOssIpPort + oss.data.data.fileName;
                    this.$set(item, 'coverUrl', coverUrl);
                }
            });
            this.total = res.data.total;
        },
        back() {
            uni.navigateBack();
        },
        toDetail(id) {
            uni.navigateTo({
                url: '/pages/product/detail?id=' + id
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.product-list__view {
    display: flex;
    flex-wrap: wrap;
    width: 720rpx;
    .product-item {
        .product-cover__image {
            width: 120rpx;
            height: 120rpx;
        }
        .product-name__view {
            text-align: center;
        }
    }
}

.page {
    position: fixed;
    bottom: 10px;
}
</style>
